body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  color: #333;
}

.app {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.file-inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.file-input {
  width: 48%;
  position: relative;
}

.file-input input[type='file'] {
  display: none;
}

.file-input label {
  display: block;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.file-input label:hover {
  background-color: #0056b3;
}

.content {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.file-container {
  width: 48%;
  display: flex;
  flex-direction: column;
  background: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.file-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.file-header h2 {
  margin: 0;
}

.change-counter {
  font-size: 0.9em;
  color: #666;
}

.tree-container,
.table-container {
  height: 400px;
  overflow-y: auto;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  border-radius: 5px;
  background: white;
}

.tree-node {
  margin-left: 20px;
  padding: 5px 10px;
}

.toggle {
  cursor: pointer;
  user-select: none;
  margin-right: 5px;
}

.node-content {
  cursor: pointer;
}

.virtual-scroll-content {
  will-change: transform;
}

table {
  border-collapse: collapse;
  width: 100%;
}

td {
  border: 1px solid #ddd;
  padding: 8px;
}

.editable:hover {
  background-color: #e6f3ff;
  cursor: text;
}

.changed {
  background-color: #ffe6e6;
}

.changed-text {
  color: #ff0000;
}

.highlight {
  background-color: #ffff99;
}

.save-button {
  margin-top: 10px;
  padding: 10px 15px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-button:hover {
  background-color: #45a049;
}
